<template>
  <div id="login">
    <h1>This is a test Page</h1>
    <h1>request is {{i}}</h1>
    <button @click="test2">test2</button>
    <button @click="test3">test3</button>
    <button @click="test4">test4</button>
    <button @click="download">download</button>
  </div>
</template>
<script>
import { postDownload } from "@/common/utils/tools";
export default {
  name: 'Login',
  data() {
    return {
      i: 0
    };
  },
  computed: {
  },
  methods: {
    test2() {
      let param = { "groupId": "230151003840", "region": "230" }
      this.request.postJson(this.blogapi.queryCustomInfo, param).then(res => {
        console.log(res)
      }).catch(error => {
        console.log(error)
      });
    },
    test3() {
      //get 测试
      this.request.getJson("/op/request2", { id: "444444" }).then(res => {
        console.log(res)
      }).catch(error => {
        console.log(error)
      });
    },
    test4() {
      let param = { "groupId": "230151003840", "region": "230" }
      this.request.postJson(this.blogapi.queryLogList, param).then(res => {
        console.log(res)
      }).catch(error => {
        console.log(error)
      });
    }, download() {
      let param = { "system": "", "service": "", "method": "" }
      postDownload(param, this.blogapi.interExport)
    }
  },
  mounted() {
  }
};
</script>
<style>
</style>


